import {useState, useEffect, useMemo, useRef} from 'react'
import {useStyles} from './contentItem.js'
import {
 Box,
 Grid,
 Typography,
 Button
} from '@material-ui/core';

import YouTubeIcon from '@material-ui/icons/YouTube'
import LinkItem from './LinkItem'


const ContentItem = ({title, description, image, links})=>{
    // LINKS IS A JSOM
    const styles = useStyles()
    return (
        <Box className={styles.mainDiv}>
            <Grid container direction="row">
                <Grid item md={3}>
                    <div className={styles.imageBox}>
                        <img src={image} className={styles.image}/>
                    </div>
                    <Grid container direction="row" style={{marginLeft:"15%"}}> 
                    {
                        Object.keys(links).map( (itemKey, index)=>{
                            return (
                            <div style={{border: "black solid 1px", borderRadius:"0.25vw", marginLeft:"1%", marginRight:"1%", marginBottom:"1%"}}>
                            <LinkItem  iconType={itemKey} link={links[itemKey]} />
                            </div>
                            )
                        })
                    }
                    </Grid>
                </Grid>
                <Grid item md={9}>
                    <Grid container direction="column"> 
                        <Box style={{paddingTop:"1%"}}>
                            <Typography component="h1" variant="h4" >
                                        {title} 
                            </Typography>
                        </Box>
                        <Box style={{paddingTop:"1%",width:"100%"}} >
                            <Typography component="p" variant="h6" >
                                        {description} 
                            </Typography>
                        </Box>
                    </Grid>
                </Grid>
            </Grid>
        </Box>


    )
}

export default ContentItem