import {makeStyles} from '@material-ui/core'


export const useStyles = makeStyles({
   box:{
       backgroundColor:"#CFCFCF",
       borderRadius:"1vw",
       padding:"2.5% 2.5% 2.5% 2.5%",
       margin:"2.5%"
   },
   image:{
       width:"20vw",
       height:"15vw",
       objectFit:"cover",
       border:"solid 1.5px",
       borderRadius:"0.5vw",
       userDrag:"none",
       pointerEvents:'none',
   },
   titleText:{
       width:"19vw",
       textOverflow:"elipses",
       textAlign:"center",
       fontSize:"1.25vw",
       marginLeft:"5%"
   },
   hline:{
       border:"solid 0.5px"
   },
   btn:{
       backgroundColor:"#808080",
       border:"solid white 0.1px",
       borderRadius:"0.1vw",
       color:"white",
       width:"90%",
       height:"120%",
       marginLeft:"5%",
       fontFamily:"IBM Plex Sans",
       "&:hover":{
        color:"black",
        border:"solid 0.1px"
       }
   },
   linkless : {
    color: "black",
    textDecorationStyle:"none",
    textDecoration: "none"
}
})

