import {useState, useEffect} from 'react'
import {
    Box,
    Grid,
 Typography,
 Button
} from '@material-ui/core';
import {useStyles} from './cardStyles'
const Card = (props)=>{
    const styles = useStyles()
    return (
        <Box className={`${styles.box}`}>
            <img className={styles.image} src={props.image}/>
            <Typography className={styles.titleText}>{props.title}</Typography>
            <hr className={styles.hline}/>
            <Grid container sx={{ display: 'inline-flex', flexDirection:"row" }} >
                {Object.keys(props.links).map(key=>{
                return (
                <Grid md={12/Object.keys(props.links).length} sx={{justifyContent:"center", margin:"1%"}}> 
                    <a href={props.links[key]} target="_blank" rel="noopener noreferrer" className={styles.linkless}>
                    <Button variant='fab' className={styles.btn}>
                        
                      {key}
                    </Button>
                    </a>
                </Grid>
                 )
                })}
            </Grid>
        </Box>
    )
}
export default Card