import {makeStyles, createTheme} from '@material-ui/core'


export const useStyles = makeStyles({
    bg: {
        width:"100%", 
        height:"92.5vh",
        display:"block",
        objectFit:"cover",
        userDrag:"none",
        pointerEvents:'none',
    },
    bgDiv:{
        position:"absolute",
        width:"100%",
        zIndex:"-1",
    },
    footerHeading:{
        color:"white",
        fontSize:"2vw",
        fontWeight:300,
        paddingLeft:"10%",
        paddingBottom:"1%"        
    },
    iconBtn:{
        color:"white",
        backgroundColor:"black",
        fontSize:"3.5vw",
        marginLeft:"5%",
        "&:hover":{
            backgroundColor :"#808080",
            
        }
    },
    itemBox:{
        marginLeft:"10%"
    },
    mainStyle:{
        outline: "solid 1px white"
    }
})

