import {useState, useEffect} from 'react'
import {CircularProgress, FormControl, Select, InputLabel, MenuItem, Grid, Typography} from '@material-ui/core'
import {useStyles} from './dataSectionStyles'
import ContentItem from '../../Shared/ContentItem/ContentItem.jsx'

import axios from 'axios'

const DataSection =(props)=>{
    // @props: data, pageTitle*

    const [isAscending, setIsAscending] = useState(true)
    const [data, setData] = useState([])
    const [isLoading, setLoading] = useState(true)
    const styles = useStyles()
    const handleChange = (e) =>{
        // alert(val)
        setIsAscending(e.target.value)
        if(e.target.value){
            sortAscending(data)
        }else{
            sortDescending(data)
        }
    }
    
    const sortAscending=(json)=>{
        json.sort(function(a, b){
            return a.timestamp - b.timestamp;
        });
    }
    const sortDescending = (json) =>{
        json.sort(function(a, b){
            return b.timestamp - a.timestamp;
        });
    }


    useEffect(()=>{
        setLoading(true)
        axios.get(props.data).then(res=>{
            setData(res.data)
            setLoading(false)
            
        }).catch(e=>{
            console.log(e)
            setLoading(false)
        })
    },[props.data])
    
    return (
         <div>
            
            <Grid container display="flex" style={{marginTop:"2%", marginBottom:"2%"}}>
                <Grid item md={1}>

                </Grid>
                <Grid item md={6} >
                    <div  className={styles.headingDiv}>
                        <Typography className={styles.headingText}>{props.title}</Typography>
                    </div> 
                </Grid>
                <Grid item md={1}>

                </Grid>
                <Grid item md={2}>
                    <div className={styles.headingDiv} style={{paddingLeft:"15%"}}>
                       <FormControl fullWidth classNamme={styles.headingText}>
                        <InputLabel id="demo-simple-select-label">Sort (Date Uploaded)</InputLabel>
                            <Select
                                labelId="demo-simple-select-label"
                                id="demo-simple-select"
                                value={isAscending}
                                label="Date Uploaded"
                                onChange={handleChange}
                            >
                                <MenuItem value={true}>Ascending</MenuItem>
                                <MenuItem value={false}>Descending</MenuItem>
                            </Select>
                        </FormControl>
                    </div> 
                </Grid>
            </Grid>
            <div style={{paddingLeft:"5%", paddingRight:"5%"}}>  
            {
                isLoading ? <CircularProgress style={{color:"white", marginBottom:"50vh"}} size="5rem"/>:
                JSON.stringify(data) === JSON.stringify([]) ?
                <div  className={styles.headingDiv}>
                    <Typography className={styles.headingText}>Nothing found!</Typography>
                </div> 
                :
                data.map(item=>{
                    return (
                        <div>
                            <ContentItem {...item} />
                        </div>
                    )
                })
            }
            </div>
         </div>

    )
}

export default DataSection