import {useEffect, useState} from 'react'
import {Link} from 'react-router-dom'
import {matchRoutes, useLocation, useNavigate} from 'react-router-dom'
import {
    AppBar, Box, Toolbar,
    Typography, Grid, Paper,
    Button, makeStyles, createTheme, ThemeProvider, Container
} from '@material-ui/core';
import {theme, useStyles} from './navbarStyles'

const Navbar = ()=>{
    const [currentLink, setCurrentLink] = useState("home")
    const navigate = useNavigate()
    const location = useLocation()

    
    const styles = useStyles()

   
    const handleLinkClick = (type)=>{
        setCurrentLink(type)
        if(type == "home"){
            navigate("/")
        }else{
            navigate(type)
        }
        

    }
    const isCurrentLink=(link)=>{
        return link == currentLink
    }
    useEffect(()=>{
        if(location.pathname === "/rl"){
            setCurrentLink("rl")
        }
        if(location.pathname === "/web3"){
            setCurrentLink("web3")
        }
        if(location.pathname === "/blog"){
            setCurrentLink("blog")
        }
        if(location.pathname === "/contact"){
            setCurrentLink("contact")
        }

    },[])
    return (
        
        <AppBar color="primary" className={styles.bar} style={{position:"relative"}}>
            <Toolbar > 
                <Grid container > 
                    <Grid  style={{marginLeft:"5%", marginRight:"30%"}}>
                        <Box>
                                <Typography className={styles.link} variant="p" component="p" onClick={()=>handleLinkClick("home")}>RAUFIE</Typography>
                        </Box>
                    </Grid>
                    {/* remaining links */}
                    <Grid className={styles.padding}>
                        <Box>
                                <Typography className={styles.link}variant="p" component="p" 
                                onClick={()=>handleLinkClick("home")}
                                style={{fontWeight:isCurrentLink("home")?"bold":""}}>Home</Typography>
                        </Box>
                    </Grid>
                    <Grid className={styles.padding}>
                        <Box>
                                <Typography className={styles.link}variant="p" component="p" 
                                onClick={()=>handleLinkClick("rl")}
                                style={{fontWeight:isCurrentLink("rl")?"bold":""}}>Deep Learning(w/ RL)</Typography>
                        </Box>
                    </Grid>
                    <Grid className={styles.padding}>
                        <Box>
                                <Typography className={styles.link} variant="p"
                                onClick={()=>handleLinkClick("web3")}
                                component="p" style={{fontWeight:isCurrentLink("web3")?"bold":""}}>Web3</Typography>
                        </Box>
                    </Grid>
                    <Grid className={styles.padding}>
                        <Box>
                                <Typography className={styles.link}variant="p" component="p" 
                                onClick={()=>handleLinkClick("blog")}
                                style={{fontWeight:isCurrentLink("blog")?"bold":""}} >Blog</Typography>
                        </Box>
                    </Grid>
                    <Grid className={styles.padding}>
                        <Box>
                                <Typography className={styles.link}variant="p" component="p" 
                                onClick={()=>handleLinkClick("contact")}
                                style={{fontWeight:isCurrentLink("contact")?"bold":""}} >Contact</Typography>
                        </Box>
                    </Grid>
                </Grid>
            </Toolbar>

        </AppBar>
 

    )
}
export default Navbar;