import {makeStyles, createTheme} from '@material-ui/core'


export const useStyles = makeStyles({
    img: {
        userDrag:"none",
        pointerEvents:'none',
        height:'3.5vw',
        width:'3.5vw',
        
    },
    div :{
        padding:"25%",
        borderRadius:"50%", 
        width:"100%",
        backgroundColor:"#D9D9D9",
        cursor:"pointer",
        border:"solid 1px",
        "&:hover":{
            transitionDuration:"0.4s",
            backgroundColor:"#FFF685",
            border:"solid black 2px",
        },
       
        
    }
})

