
import {useStyles} from './footerStyles'
import {
Box,
 Grid,
 Typography,
 Button
} from '@material-ui/core';
import {useState} from 'react'
import ContentCopyIcon from '@mui/icons-material/ContentCopy';
import EmailIcon from '@mui/icons-material/Email';
import TwitterIcon from '@mui/icons-material/Twitter';
import GitHubIcon from '@mui/icons-material/GitHub';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import YouTubeIcon from '@mui/icons-material/YouTube';
const Footer = ()=>{
const styles = useStyles()
    const [isCopied, setCopied] = useState(false)
    const iconStyle= {
        color:"white", 
        fontSize:"3.5vw",
    }
    const copyToClipboard=(text)=>{
        navigator.clipboard.writeText(text)
        setCopied(true)
        setTimeout(()=>{
            setCopied(false)
        }, 3000)
    }
    const openLink = (link)=>{
        window.open(link)
    }
    const links = [{
        toOpen :  "https://mail.google.com/mail/?view=cm&fs=1&to=abdulraufbusiness@gmail.com&su=SUBJECT&body=BODY",
        title:"Send Mail",
        toCopy :  "abdulraufbusiness@gmail.com",
        icon:<EmailIcon color="primary" sx={iconStyle}/>
    },
    {
        toOpen :  "https://twitter.com/raufie_original",
        title:"Open Twitter",
        toCopy :  "https://twitter.com/raufie_original",
        icon:<TwitterIcon color="primary" sx={iconStyle}/>
    },
    {
        toOpen :  "https://github.com/raufie",
        title:"Open Github",
        toCopy :  "https://github.com/raufie",
        icon:<GitHubIcon color="primary" sx={iconStyle}/>
    },
    {
        toOpen :  "https://www.youtube.com/channel/UC93sfFCG4euSAWO6kYxheUQ",
        title:"Open Youtube",
        toCopy :  "https://www.youtube.com/channel/UC93sfFCG4euSAWO6kYxheUQ",
        icon:<YouTubeIcon color="primary" sx={iconStyle}/>
    },
]
    return (
        <Box className={styles.mainStyle} style={{height:"30vh"}}>
            <Box className={styles.bgDiv}> 
                    <img className={styles.bg} style={{height:"30vh"}} src={process.env.PUBLIC_URL+"/home_banner_bg.svg"}/>
            </Box>
            <Grid container>
                <Grid md={6}>
                    <Typography className={styles.footerHeading} style={{paddingTop:"5%"}} ><b>About</b></Typography>
                    <Typography className={styles.footerHeading} >By Abdul Rauf @2022</Typography>
                    <Grid item> 
                        <Typography className={styles.footerHeading} >
                            Feel free to contact via email: 
                            <Button style={{color:"white"}} onClick={()=>copyToClipboard("abdulraufbusiness@gmail.com")}> Copy Email<ContentCopyIcon /></Button>
                            {isCopied?<CheckCircleIcon/> :null}
                            </Typography>
                        
                    </Grid>

                </Grid>
                
            </Grid> 
            
            
            
            
        </Box> 
    )

}
export default Footer