import {useState, useEffect} from 'react'
import {useStyles} from './sectionsStyles'
import {
    Box,
 Grid,
 Typography
} from '@material-ui/core';

import Card from '../../Shared/Card/Card'

const Section = (props)=>{
    const styles = useStyles()
    return (
        <Box > 
            <Box className={styles.headingDiv}>
                <Box >
                    <img src={process.env.PUBLIC_URL+"/headingbanner.svg"} className={styles.heading} />
                </Box>
                <Typography className={styles.headingText}>{props.data.title}</Typography>
            </Box>
            <Grid container sx={{display:"flex"}} >
                {
                    props.data.data.map(item=>{
                        return (
                        <Card 
                            image={item.image}
                            title={item.title}
                            links={item.links}
                            
                        />
                    )
                    })
                }
            </Grid>
        </Box>
    )
}

export default Section
