import {useRef, useEffect} from "react"
import YouTubeIcon from '@material-ui/icons/YouTube'
import {useStyles} from './contentItem'
const LinkItem =(props)=>{
    const styles=  useStyles()
    const getRef = ()=>{
        if (props.iconType === "youtube"){
            return <img src={process.env.PUBLIC_URL+"/youtubeicon.svg"} style={{ color:"white", marginTop:"0.4vw", marginLeft:"0.05vw",
            width:"5vh"
           }}/>
        }
        else if (props.iconType ==="medium"){
            return <img src={process.env.PUBLIC_URL+"/mediumicon.svg" }style={{ color:"white", marginTop:"0.05vw", marginLeft:"0.05vw",
            width:"5vh"
           }}/>
        }
        else if (props.iconType ==="github"){
            return <img src={process.env.PUBLIC_URL+"/githubicon.svg"} style={{ color:"white", marginTop:"0.05vw", marginLeft:"0.05vw",
            width:"5vh"
           }}/>
        }
        else {
            return <img src="/webapp.svg" style={{ color:"white", marginTop:"0.05vw", marginLeft:"0.05vw",
            width:"5vh"
           }}/>
        }
    }
    const iconRef = useRef(getRef())

    
    
    return (
        <div className={styles.a}>
            <a href={props.link} target="_blank" className={styles.linkless}> 
                <div style={{ }}> 
                    {iconRef.current}
                </div>
            </a>
        </div>
    )

        
}
export default LinkItem